"use client";

import React, { useState } from "react";
import Link from "next/link";
/* @ts-ignore */
import { motion } from "framer-motion";

const navLinks = [
  { name: "Product", href: "#" },
  { name: "Docs", href: "#" },
  { name: "Enterprise", href: "#" },
  { name: "Sign In", href: "#" },
];

const Navbar = () => {
  const [mobileMenuOpen, setMobileMenuOpen] = useState(false);

  return (
    <motion.header
      initial={{ y: -100, opacity: 0 }}
      animate={{ y: 0, opacity: 1 }}
      transition={{ duration: 0.5 }}
      className="fixed top-0 left-0 right-0 z-50 bg-gradient-to-b from-black to-[#1a1a1a] bg-opacity-95 backdrop-blur-md shadow-lg border-b border-gray-800"
    >
      <nav className="container mx-auto flex items-center justify-between px-6 py-5">
        {/* Logo */}
        <Link href="/" className="text-white font-bold text-2xl tracking-tight hover:text-primary transition-colors">
          Cursor
        </Link>

        {/* Desktop Nav */}
        <ul className="hidden md:flex space-x-10 items-center text-gray-300">
          {navLinks.map((link) => (
            <li key={link.name}>
              <Link href={link.href} className="hover:text-white transition-colors">
                {link.name}
              </Link>
            </li>
          ))}
          <li>
            <Link
              href="#"
              className="bg-white hover:bg-gray-100 text-black px-5 py-2.5 rounded-lg font-semibold transition-colors"
            >
              Get Cursor
            </Link>
          </li>
        </ul>

        {/* Mobile Menu Button */}
        <button
          className="md:hidden text-gray-300 hover:text-white focus:outline-none transition-colors p-2 -mr-2"
          onClick={() => setMobileMenuOpen(!mobileMenuOpen)}
          aria-label="Toggle menu"
        >
          <svg
            className="w-6 h-6"
            fill="none"
            stroke="currentColor"
            strokeWidth={2}
            strokeLinecap="round"
            strokeLinejoin="round"
            viewBox="0 0 24 24"
          >
            {mobileMenuOpen ? (
              <path d="M6 18L18 6M6 6l12 12" />
            ) : (
              <path d="M4 6h16M4 12h16M4 18h16" />
            )}
          </svg>
        </button>
      </nav>

      {/* Mobile Menu */}
      {mobileMenuOpen && (
        <motion.ul
          initial={{ height: 0, opacity: 0 }}
          animate={{ height: "auto", opacity: 1 }}
          transition={{ duration: 0.3 }}
          className="md:hidden bg-[#1a1a1a] bg-opacity-95 backdrop-blur-md shadow-lg px-6 py-6 space-y-5 text-gray-300 border-b border-gray-800"
        >
          {navLinks.map((link) => (
            <li key={link.name}>
              <Link
                href={link.href}
                className="block hover:text-white transition-colors text-lg"
                onClick={() => setMobileMenuOpen(false)}
              >
                {link.name}
              </Link>
            </li>
          ))}
          <li className="pt-2">
            <Link
              href="#"
              className="block bg-white hover:bg-gray-100 text-black px-5 py-3 rounded-lg font-semibold transition-colors text-center text-lg"
              onClick={() => setMobileMenuOpen(false)}
            >
              Get Cursor
            </Link>
          </li>
        </motion.ul>
      )}
    </motion.header>
  );
};

export default Navbar;
